import {
	mapState,
	mapMutations
} from 'vuex'

export default {
	computed: {
		...mapState(['hasLogin', 'forcedLogin', 'userName', 'realName', 'orgCode', 'orgName'])
	},
	methods: {
		...mapMutations(['logout']),
		bindLogin() {
			uni.navigateTo({
				url: '../..login/login',
			});
		},
		bindLogout() {
			this.logout();
			uni.removeStorageSync('userInfo');
			/**
			 * 如果需要强制登录跳转回登录页面
			 */
			if (this.forcedLogin) {
				uni.reLaunch({
					url: '../../login/login',
				});
			}
		},
		goDetail: function(e) {
			uni.navigateTo({
				url: e
			})
		},
		wxGetUserInfo(e) {
			console.log("得到用户信息", e);
			if (e.detail.errMsg !== 'getUserInfo:ok') {
				uni.showModal({
					title: "获取用户信息失败",
					content: "错误原因" + e.detail.errMsg,
					showCancel: false
				});
				return;
			}
			// 				this.hasUserInfo = true;
			// 				this.userInfo = e.detail.userInfo
			uni.showModal({
				title: "成功",
				content: "openid:" + e.detail.userInfo.openid,
				showCancel: false
			});
		}
	}
}
